import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function incomeQualityTtm(tableName: string) {
  const title = "Income Quality (LTM)"
  return genericColumn({
    endpoint: "companyKeyMetrics",
    columnKeys: ["incomeQualityTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "How real and cash-based the earnings are.",
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyKeyMetrics}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about income quality or quality of earnings measures?`,
              value: info.row.original.financialData.companyKeyMetrics?.data?.incomeQualityTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            (info.row.original.financialData.companyKeyMetrics?.data?.incomeQualityTtm ?? 0) * 100
          }
        />
      )
    },
  })
}

export { incomeQualityTtm }
